import SearchUser from './SearchUser'
import List from './List'
import Logout from './Logout'

function RecipientList (props) {
    return (
        <div className='recipient-list col border-end border-success border-curve-10'>
            <SearchUser></SearchUser>
            <List changeCurrentRecipient={props.changeCurrentRecipient}></List>
            <Logout></Logout>
        </div>
    )
}

export default RecipientList